import ServiceProvider from './ServiceProvider';
export default class ValidateServiceProvider extends ServiceProvider {
    constructor(app) {
        super(app);
    }
    register() {
        let validate = this.validates();
        this.app.register('validater', validate);
    }
    validates() {
        return {
            password(rule, value, callback) {
                if(value === '') {
                    callback();
                    return;
                }
                let regular = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
                if(!regular.test(value) && process.env.NODE_ENV === "production") {
                    callback(new Error('61111'));
                } else {
                    callback();
                }
            },

            unsignedInt(rule, value, callback) {
                if(value === '') {
                    callback();
                    return;
                }
                let regular = /^([1-9][0-9]*)$/;
                if(!regular.test(value)) {
                    callback(new Error('0'));
                } else {
                    callback();
                }
            },
            //0
            number(rule, value, callback) {
                if(value === '') {
                    callback();
                    return;
                }
                let regular = /^[0-9]+([.]{1}[0-9]{1,2})?$/;
                if(!regular.test(value)) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            },
            //
            mobile(rule, value, callback) {
                if(value === '') {
                    callback();
                    return;
                }
                let regular = /^1[3|4|5|7|8]\d{9}$/;
                if(!regular.test(value)) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            },
            //
            email(rule, value, callback) {
                if(value === '') {
                    callback();
                    return;
                }
                let regular = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
                if(!regular.test(value)) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            },
            //
            disscount(rule, value, callback) {
                if(value === '') {
                    callback();
                    return;
                }
                let regular = /[\d]?(\.[\d]{0,2})?/;
                if(!regular.test(value)) {
                    callback(new Error('0102'));
                } else {
                    callback();
                }
            }
        }
    }
}
