<template>
    <el-col :span= "24" class="toolbar">
        <div class="special-rule">
            <p></p>
        </div>
        <!---->
        <el-button size="small" type="primary" @click="create()" style="margin-bottom: 10px;"></el-button>
        <!---->
        <el-table  highlight-current-row :data="rules">
            <el-table-column prop="condition" label="" min-width="180">
                <template slot-scope = "scope">
                    {{ condition(scope.row) }}
                </template>
            </el-table-column>
            <el-table-column prop="score" label="" min-width="100"></el-table-column>
            <el-table-column prop="updatedAt" label="" min-width="180"></el-table-column>
            <el-table-column prop="totalReceivedScore" label="" min-width="100"></el-table-column>
            <el-table-column label="" width="100">
                <template slot-scope="scope">
                    <el-button size="small" @click="edit(scope.row.id)" type="text"></el-button>
                    <el-button size="small" @click="delete(scope.row)" type="text"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </el-col>
</template>
<script>
    const FOLLOW = 9;
    const ORDER_COUNT = 11;
    const ORDER_AMOUNT = 10;
    export default {
        data() {
            return {

            };
        },
        props: {
            rules: {
                default: null,
                type: Array
            }
        },
        computed: {
        },
        methods: {
            create() {
                this.$router.push({
                    name: 'rule-create',
                    params: {
                        projectId: this.$requestInput('projectId')
                    }
                });
            },
            edit(id) {
                this.$router.push({
                    name: 'rule-editor',
                    params: {
                        'projectId': this.$requestInput('projectId'),
                        'scoreRuleId': id
                    }
                })
            },
            delete(id) {
                this.$command('DELETE_SCORE_RULE', id);
            },
            condition(condition) {
                switch (condition['type']) {
                    case FOLLOW:
                        return '';
                    case ORDER_COUNT:
                        return `${condition['orderCount']}`;
                    case ORDER_AMOUNT:
                        return `${condition['orderAmount']}`;
                }
            }
        }
    }
</script>
<style scoped>
    .special-rule p{border-left: 4px solid #f70;padding-left:12px}
    .special-rule{background: #f5f5f5;padding:12px;margin:10px 0;color:#000;font-size: 14px;font-weight: 700;}
</style>
