<template>
    <search-header @search = "search">
        <template slot = "searchInput">
            <el-form-item prop="orderCode" label="" >
                <el-input size="small" v-model="orderCode" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="receiverName" label="" >
                <el-input size="small" v-model="receiverName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="receiverMobile" label="" >
                <el-input size="small" v-model="receiverMobile" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="beginAt" label="">
                <el-date-picker v-model="beginAt" type="date" :editable="false" placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item label="" label-width="0px">
                
            </el-form-item>
            <el-form-item prop="endAt" label="" >
                <el-date-picker v-model="endAt" type="date" :editable="false" placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item prop="orderStatus" label="">
                <el-select size="small" v-model="orderStatus" placeholder="">
                    <el-option v-for="(value, key) in statusDict" :value="key" :label="value" :key="value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item prop="payType" label="">
                <el-select size="small" v-model="payType" placeholder="">
                    <el-option v-for="(value, key) in payTypes" :value="key" :label="value" :key="value"></el-option>
                </el-select>
            </el-form-item>
        </template>
    </search-header>
</template>
<script>
    import SearchHeader from '@/components/SearchHeader';
    import ORDER_STATUS from '../OrderStatus';
    import PAYMENT_TYPES from '../Payment';
    import _ from 'underscore';
    export default {
        components: {
            'search-header': SearchHeader
        },
        props: {
            value: {
                default: null,
                type: Object
            }
        },
        data() {
            return {
                receiverName: null,
                orderCode: null,
                receiverMobile: null,
                beginAt: null,
                endAt: null,
                orderStatus: 'ALL',
                payType: 'ALL',
                payTypes: {
                    "ALL": '',
                    "UNKNOWN": '',
                    "ALI_PAY": '',
                    "WECHAT_PAY": ''
                },
                statusDict: {
                    "ALL": "",
                    "WAIT_PAY": "",
                    "PAID": "",
                    "COMPLETED": "",
                    "REFUNDING": "",
                    "REFUSE_REFUND": "",
                    "REFUNDED": "",
                    "ORDER_CANCEL": ""
                }
            };
        },
        watch: {
            value: {
                deep: true,
                handler(search) {
                    if(search) {
                        this.initSearchData(search);
                    }
                }
            }
        },
        created() {
            if(this.value && !_.isEmpty(this.value)) {
                this.initSearchData(this.value);
            }
        },
        methods: {
            search () {
                let search = this.buildSearchData();
                this.$emit('search', search);
            },
            initSearchData(search) {
                console.log('search ', search);
                this.receiverName  =  search['receiver_name'] ;
                this.receiverMobile  =  search['receiver_mobile'] ;
                this.orderCode   =   search['code'];
                this.beginAt =  search['paid_at'] && search['paid_at'][1] && search['paid_at'][1]['value'] ? search['paid_at'][0]['value'] : null;
                this.endAt = search['paid_at'] && search['paid_at'][1] && search['paid_at'][1]['value'] ? search['paid_at'][1]['value'] : null;

                for(let index in ORDER_STATUS) {
                    let value = ORDER_STATUS[index];
                    if(value == search['status']) {
                        this.orderStatus = index;
                        if(index === 'WAIT_SEND' || index === 'PAID') {
                            this.orderStatus = 'PAID';
                        }
                    }
                }

                for (let index in PAYMENT_TYPES) {
                    let value = PAYMENT_TYPES[index];
                    if(value == search['pay_type']) {
                        this.payType = index;
                    }
                }
            },
            buildSearchData() {
                let search = {
                    "paid_at": [
                        {
                            'opt': '>='
                        },
                        {
                            'join': 'and',
                            'opt': '<'
                        }
                    ],
                    "type": 0
                };
                if(this.receiverName)
                    search['receiver_name'] = this.receiverName;
                if(this.receiverMobile)
                    search['receiver_mobile'] = this.receiverMobile;
                if(this.orderCode)
                    search['code'] = this.orderCode;
                if(this.beginAt) {
                    search['paid_at'][0]['value'] = this.beginAt;
                }
                if(this.endAt)
                    search['paid_at'][1]['value'] =  this.endAt;
                if(this.orderStatus && ORDER_STATUS[this.orderStatus])
                    search['status'] = ORDER_STATUS[this.orderStatus];
                if(this.payType && PAYMENT_TYPES[this.payType])
                    search['pay_type'] = PAYMENT_TYPES[this.payType];
                return search;
            }
        }
    }
</script>
