import Route from './Route';
import Layout from '../views/common/Layout';
import Index from '../views/categories/Index';
let route = new Route();
route.group('/project/:projectId/categories', {
    'tag' : '',
    'component': Layout,
    uses : function(parent) {
      parent.addRoute('/', {
        'name': 'categories',
        'component': Index,
        'tag': ''
      });
    }
});
export default route;
