<template>
    <div>
        <el-table highlight-current-row :data="members">
            <el-table-column prop="idx" label="#" />
            <el-table-column prop="nickname" label="" />
            <el-table-column prop="mobile" label="" />
            <el-table-column prop="ordersCount" label="" />
            <el-table-column prop="card" label="" />
            <el-table-column prop="totalScore" label="" />
            <el-table-column prop="score" label="" />
            <el-table-column prop="canUseScore" label="" />
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button :key = "scope.row.id" type="primary" size="mini" @click="memberCard = !memberCard" :disabled="true"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <member-card :show="memberCard" @open="showMemberCard" @close="closeMemberCard" />
    </div>
</template>
<script>
import SetMemberCard from "./SetMemberCard";
export default {
    components: {
        'member-card': SetMemberCard
    },
    props: {
        members: {
            default: null,
            type: Array
        }
    },
    data() {
        return {
            memberCard: false
        };
    },
    methods: {
        showMemberCard() {
            this.memberCard = true;
        },
        closeMemberCard() {
            this.memberCard = false;
        }
    }
}
</script>
