<template>
	<el-dialog
			title=""
			:visible.sync="dialogShow"
			width="30%"
			@open="open"
			@close="close"
			center>
		<span>
			
		</span>
		<span slot="footer" class="dialog-footer">
			<el-button @click="close"> </el-button>
			<el-button type="primary" @click="deleteProject"> </el-button>
		</span>
	</el-dialog>
</template>
<script>
    export default {
        props: {
            show: {
                default: false,
                type: Boolean
            },
            project: {
                default: null,
                type: Object
            }
        },
        watch: {
            show(value) {
                this.dialogShow = value;
            }
        },
        data() {
            return {
                dialogShow: this.show
            };
        },
        methods: {
            async deleteProject() {
                console.log(this.project, this.http);
                let result = await this.http.projects.delete(this.project.id);
                if(result) {
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                    await this.$command('RELOAD');
                    this.close();
                } else {
                    this.$message({
                        message: '',
                        type: 'error'
                    });
                }
            },
            open() {
                this.dialogShow = true;
                this.$emit('open');
            },
            close() {
                this.dialogShow = false;
                this.$emit('close');
            }
        }
    }
</script>
