<template>
  <div class="chart">
    <p class="chart-title"></p>
    <div class="chart-box">
      <chart :resizable="true" :option="options"></chart>
    </div>
  </div>
</template>
<script>
    import IEcharts from 'vue-echarts-v3'
    import _ from 'underscore';
    export default {
        name: 'SevenDaysStatics',
        props: ['projectId'],
        components: {
            "chart": IEcharts
        },
        data(){
            return {
                default: {
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data: [],
                    },
                    grid: {
                        top: '10%',
                        left: '6%',
                        right: '6%',
                    },
                    color: ["#20a0ff", "#13CE66", "#F7BA2A", "#FF4949"],
                    xAxis: {
                        type: 'category',
                        data: ['', '', '', '', '', '', '']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [],
                        type: 'line',
                        name: ''
                    },{
                        data: [],
                        type: 'line',
                        name: ''
                    }]
                },
                options: {

                }
            };
        },
        watch: {
            project(value) {
                if(value) {
                    this.sevenDaysCount();
                }
            }
        },
        mounted () {

        },
        computed:{
            project() {
                return this.$store.getters['projects/currentProject'];
            }
        },
        methods:{
            async sevenDaysCount() {
                let id = this.project ? this.project.id : null;
                if(!id) return;
                let result = await this.http.projects.sevenDaysCount(id);
                this.options =  _.extend(this.default, {
                    series: [{
                        data: result['last_week'],
                        type: 'line',
                        name: ''
                    },{
                        data: result['this_week'],
                        type: 'line',
                        name: ''
                    }]
                });
            }
        },
        created(){
        }
    }
</script>
<style scoped>
  .chart-box{border:1px solid gainsboro;height:400px;}
</style>
